@import '../../styles';

.container{
  @include display(column,center,center);
}

.margin{
  margin: 0 2em;
 }

.detail{
  width: 45%;
  padding-bottom: 3%;
  border-radius: 10% 10% 0 0;
  background-color: #ffffff;
 @include display(row,center,center);
}

.id__container{
margin:10px 50px;
@include display(row,flex-end,center);
}


h1 {
  font-family: $primary;
 margin: 20px 0 20px 0;
 color: white;
}
h3 {
  font-family: $primary;
  font-weight: 600;
  color: #ffffff;
}

h4{
  font-family: $primary;
  margin:0;
  color: #5c5c5c;
}
p{
  font-family: $second;
  @include font(12,300,#5c5c5c)
}
nav{
  display: flex;
  justify-content: space-between;
  margin: 2em;
}


 .pokemon__img-container{
   margin: 0 2em;
   @include display(row,center,center);
 
}


 .pokemon-detail__container{
  margin: 0 50px 0 50px;
   @include display(column,flex-start,flex-start);

   & img{
    max-height: 200px;
   }

   .height-weight{
     width: 100%;
     @include display(row,flex-start,flex-start);
    margin: 20px 0;
   
   }
   .row-content {
     width: 150px;
     @include display(row,flex-start,flex-start);
   }
   p{
  margin: 0 20px 0 10px;
 
   
   }
}


.types__container{
  @include display(row,flex-start,flex-start);

  .types-row__container{
    @include display(row,'','');
    margin-right: 20px;
    width: 100px;
    height: 40px;
    border-radius: 15%;
  

  p{
    @include display(row,center,center);
    width: 100%;
    height: 100%;
    text-align: center;
    font-size: 16px;
    margin:0;
    color:white;
   }
  }
}


.progress__container p{
 margin:10px 0;
}


.progress-bar {
 background-color: #ffffff;
 height: 10px;
 width: 250px;
 box-shadow: 0 1px 5px rgb(219, 219, 219);
 border-radius: 5px;

 
}

.progress-bar span {
 display: inline-block;
 height: 100%;
 border-radius: 5px;
 box-shadow: 0 1px 5px rgb(219, 219, 219);
 transition: width 1s ease-in-out;
}

.stripes span {
 background-size: 20px 20px;
 margin-bottom: 10px;

}

