  @media (max-width: 1000px) {

    h1{
      margin-top:20px;
    }

    .margin{
      margin: 0 4em;
     }
    
  
    .pokemon-img{
      max-height: 200px;
      max-width: 200px
    }
  
    .detail{
      @include display(row,flex-start,center);
      width: 100%;
      height: 100%;
      padding-bottom: 3%;
